import React, { useState } from 'react'
import './login.css'
import { Link, useHistory } from 'react-router-dom'
import { auth } from './firebase'

const Login = () => {
  const history = useHistory();

  const [email, setemail] = useState('')
  const [password, setpassword] = useState('')

  const sigin = (e) => {
    e.preventDefault();

    auth.signInWithEmailAndPassword(email, password)
      .then(auth => {
        history.push('/')
      })
      .catch(error => alert(error.message))
  }

  const register = (e) => {
    e.preventDefault();
    auth.createUserWithEmailAndPassword(email, password).then((auth) => {
      console.log(auth);

      if (auth) {
        history.push('/')
      }

    })
      .catch(error => alert(error.message))



  }


  return (
    <div className='login'>
      <Link to='/'>
        <img className='login__image' src='https://static.businessworld.in/article/article_extra_large_image/1597056012_CNRvas_amazon_dkblue_noto_email_v2016_us_main_CB468775337_.png' alt='login' />


      </Link>
      <div className="login__container">
        <h1>Sign-in</h1>
        <form>

          <h5>E-mail</h5>
          <input type="text" value={email} onChange={e => setemail(e.target.value)} />
          <h5>Password</h5>
          <input type="password" value={password} onChange={e => setpassword(e.target.value)} />

          <button type='submit' onClick={sigin}
            className='login__signinbutton'>Sign-in</button>
        </form>

        <p>By signin in you are agree to the AMAZON CLONE
          see your privacy Notice ,our cookies notice and our interest-baseds ads notice
        </p>

        <button onClick={register}
          className='login__registerbutton'>create amazon account</button>


      </div>




    </div>
  )
}

export default Login
